import React from 'react';
import { Spinner } from 'react-bootstrap';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';
import Footer from './Footer';

export default function MonthlyContri(props) {

	const [monthlyIncrease, setMonthlyIncrease] = React.useState("10");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "0", "name": "No increase" },
		{ "key": "5", "name": "5%" },
		{ "key": "10", "name": "15%" },
		{ "key": "20", "name": "20%" }
	]

	const checkValidation = () => {
		if (monthlyIncrease!=='0') {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	},[monthlyIncrease])

	return (
		<div>
			<MainHeader
				header="Plan child education"
				right="Home"
				screenType="goalScreen"
				navigation={props.history}
			/>
			<ProgressBar now={80} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">By how much can you increase your monthly contribution to this goal each year?</div>
							<input
								type="tel"
								id="monthlyIncrease"
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='10%'
								value={monthlyIncrease}
								onChange={(num) => {
									const a = Number(num.target.value.replace(/\D/g, '')).toString()
									setMonthlyIncrease(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row',overflowX:'auto',paddingBottom:'10px' }}>
								{chipset.map((item) => {
									return (
										<Chip
											className={monthlyIncrease===item.key?"chip-b selected":"chip-b"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setMonthlyIncrease(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/financial-goal/name"
				state={{...props.location.state,monthlyIncrease}}
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
