import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import AdminServices from 'services/api/AdminServices';
import { MintboxSmallCube, PieChart } from "assets/SVG";
import { BsFillLockFill, BsFillUnlockFill, BsGraphUp, BsPlusCircle, BsThreeDotsVertical } from 'react-icons/bs';
import { Modal } from 'react-bootstrap';
import { setCreateMintboxData } from 'redux/CreateMintbox/actions'

const MintboxAllocation = (props) => {

    const [selectedETFs, setSelectedETFs] = React.useState([]);

    const [showHistoricalModal, setShowHistoricalModal] = React.useState(false)
    const [showMainModal, setShowMainModal] = React.useState(false)
    const [showFundModal, setShowFundModal] = React.useState(false)
    const [selectedModalFund, setSelectedModalFund] = React.useState()
    const [loading, setloading] = React.useState(false);

    React.useEffect(async () => {
        await getSelectedETFs();
    }, []);

    React.useEffect(() => {
        saveData(selectedETFs)
    }, [selectedETFs]);

    const getSelectedETFs = async () => {
        try {
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    console.log("SelectedETFS",u.selectedETFs)
                    setSelectedETFs(u.selectedETFs)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };
    const saveData = async (arr) => {
        const dataFinal = {
            selectedETFs: arr
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            setCreateMintboxData(u);
        })
    }
    const equalizeAllWeights = () => {
        const count = selectedETFs.length
        const newArr = selectedETFs.map((fund, i) => {
            if (i === 0) { return { ...fund,isLocked:false, weight: Math.floor(100 / count) + 100 % count } }
            else return { ...fund,isLocked:false, weight: Math.floor(100 / count) }
        })
        setSelectedETFs(newArr)
    }

    const equalizeUnlockedWeights = (arr) => {
        console.log("equalize")
        const unlockedETFsCount = arr.filter(f => !f.isLocked).length;
        console.log("unlockedEtfCount",unlockedETFsCount)
        if (unlockedETFsCount === 0) {
            toast.error("Unlock some ETFs to readjust weightsssss");
            return;
        }
        const lockedWeight = arr.reduce((sum, f) => {
            if (f.isLocked) {
                return sum + f.weight
            }
            return sum
        }, 0)
        console.log("lockedWeight", lockedWeight)
        const remainingWeight = 100 - lockedWeight;
        if(remainingWeight<unlockedETFsCount){
            toast.error("Unlock some ETFs to readjust weights");
            return;
        }
        const firstNotLockedIndex = arr.findIndex((f) => !f.isLocked)
        console.log('unlockedETFsCount:', unlockedETFsCount)
        console.log('remainingWeight:', remainingWeight)
        const newArr = arr.map((fund, i) => {
            if (fund.isLocked) {
                return fund;
            }
            if (i === firstNotLockedIndex) { return { ...fund, weight: Math.floor(remainingWeight / unlockedETFsCount) + remainingWeight % unlockedETFsCount } }
            else return { ...fund, weight: Math.floor(remainingWeight / unlockedETFsCount) }
        })
        console.log("newArr", newArr);
        setSelectedETFs(newArr)
    }

    const assignWeights = (index, value = 0) => {
        console.log("assign",index,value)
        if(!value){
            value = 0
        }
        let newArr = selectedETFs.map((fund,i)=>{
            if(i===index){
                return {...fund,weight:value,isLocked:true}
            }
            else return fund
        })
        equalizeUnlockedWeights(newArr)
    }

    const checkWeights = () => {
        if (selectedETFs.length < 2) return false;
        if (selectedETFs.some(f => f.weight === 0)) return false;
        if (selectedETFs.reduce((sum, f) => sum + f.weight) !== 100, 0) return false;
        return true;
    }

    const onDelete = (id) => {
        equalizeUnlockedWeights(selectedETFs.filter(fund => fund.UID !== id));
    }

    const onReplace = (fund) => {
        props.history.push({
            pathname: '/build-your-own-mintbox/replace-ETF',
            state: { fundToReplace: fund }
        })
    }

    const onLock = (id) => {
        console.log("onlock",selectedETFs)
        const newArr = selectedETFs.map(fund => {
            return fund.UID === id ? { ...fund, isLocked: !fund.isLocked } : fund
        })
        console.log("after lock",newArr)
        setSelectedETFs(newArr)
    }


    const Footer = () => {
        const onNextPress = () => {
            if (checkWeights()) {
                props.history.push('/build-your-own-mintbox/rebalancing-strategy')
            }
        }
        return (
            <>
                <div style={{ position: "fixed", bottom: 0, width: "100%" }}>
                    <div onClick={() => setShowHistoricalModal(true)} style={{ fontSize: "14px", fontWeight: '400', color: "#00A6A4", textAlign: "center", borderTop: "1px solid #e5e5e5", padding: "2% 4%", background: "#FFFFFF" }}>
                        Historical performance <BsGraphUp size={16} style={{ margin: "5px" }} />
                    </div>
                    <div style={{ fontSize: "14px", fontWeight: '400', color: "#6c6c6c", textAlign: "center", borderTop: "1px solid #e5e5e5", padding: "2% 4%", background: "#FFFFFF" }}>
                        Minimum Investment <b>RS 12344</b>
                    </div>
                    <div style={{ background: "#FAFAFA", display: "flex", borderTop: "1px solid #e5e5e5" }}>
                        <div style={{ flex: 1, padding: "4%", paddingRight: 0 }}>
                            <div style={{ fontSize: "14px", fontWeight: "400", marginBottom: "2%" }}>Select rebalancing strategy in the next step</div>
                        </div>
                        <div style={{ flex: 1, padding: "4%" }}>
                            <button
                                style={{
                                    borderRadius: 4,
                                    fontSize: "14px",
                                    backgroundColor: cssVar.getPropertyValue("--brandSecondary"),
                                    color: "#FFFFFF",
                                    height: "44px",
                                    width: "100%",
                                    border: "none",
                                    opacity: checkWeights() ? 1 : 0.4,
                                }}
                                onClick={() => { onNextPress() }}
                            >
                                Confirm weights
                            </button>
                        </div>
                    </div>
                </div>
            </>
        )
    }
    const MainModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showMainModal}
                backdrop="static"
                onHide={() => setShowMainModal(false)}
                keyboard={false}
            >
                <Modal.Body>
                    <input type="radio" name="main" value="equalize" defaultChecked /> Equalize ETF weights
                </Modal.Body>
                <Modal.Footer>
                    <button className="close" onClick={() => setShowMainModal(false)}>
                        Close
                    </button>
                    <button className="confirm" onClick={() => {
                        equalizeAllWeights();
                        setShowMainModal(false);
                    }}>
                        Confirm
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }
    const FundModal = () => {
        const [selectedOption, setSelectedOption] = React.useState()

        const closeModal = () => {
            setSelectedModalFund({});
            setShowFundModal(false);
        }
        const onConfirm = () => {
            switch (selectedOption) {
                case 'replace': {
                    onReplace(selectedModalFund);
                    setShowFundModal(false);
                    break;
                }
                case 'lock': {
                    onLock(selectedModalFund.UID)
                    setShowFundModal(false);
                    break;
                }
                case 'remove': {
                    onDelete(selectedModalFund.UID);
                    setShowFundModal(false);
                    break;
                }
                default: closeModal()
            }
        }
        return (
            <Modal
                className="bottom-drawer"
                show={showFundModal}
                backdrop="static"
                onHide={() => closeModal()}
                keyboard={false}
            >
                <Modal.Body>
                    <div className="radio-item" style={{ display: 'block' }}>
                        <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="replace" id="replace"
                            className="textNormal" />
                        <label className="textNormal radio-label" htmlFor="replace">Replace this ETF</label>
                    </div>
                    <div className="radio-item" style={{ display: 'block' }}>
                        <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="lock" id="lock"
                            className="textNormal" />
                        <label className="textNormal radio-label" htmlFor="lock">
                            {selectedModalFund?.isLocked ? 'Unlock weight of this ETF' : 'Lock weight of this ETF'}
                        </label>
                    </div>
                    <div className="radio-item" style={{ display: 'block' }}>
                        <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="remove" id="remove"
                            className="textNormal" />
                        <label className="textNormal radio-label" htmlFor="remove" style={{ color: "#EA2F2F" }}>Remove this ETF</label>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="close" onClick={() => closeModal()}>
                        Close
                    </button>
                    <button className="confirm" onClick={() => onConfirm()}>
                        Confirm
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }
    const HistoricalModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showHistoricalModal}
                backdrop="static"
                onHide={() => setShowHistoricalModal(false)}
                keyboard={false}
            >
                <Modal.Body>
                    <div style={{ fontSize: "12px", fontWeight: "400", color: "#6C6C6C" }}>
                        <b>Disclaimer:</b> Past performance is not an assurance of future
                        performance. The calculations do not take into account
                        taxation, mintbox fee and other charges like STT.
                    </div>
                    <div style={{ fontSize: "12px", fontWeight: "400", color: "#6C6C6C" }}><b>Note:</b> We have assumed a 3 month rebalancing frequency</div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="close" onClick={() => setShowHistoricalModal(false)}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }
    return (
        <div>
            <MainHeader
                header="Build your own mintbox"
                right="help"
                screenType="Invest"
                navigation={props.history}
            />
            <div className="containerData px-0 pb-4" style={{ marginBottom: "140px" }}>
                <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333", display: "flex", alignItems: "flex-start" }}>
                    <img src={PieChart} style={{ margin: "2px 10px auto 0" }} />
                    <span style={{ flex: 1 }}>Define the target weights of the ETFs you have selected for your mintbox</span>
                </div>
                <div style={{ margin: "0 4%", display: "flex" }}>
                    <div style={{ flex: 1, margin: "4% 2% 4% 0", fontSize: "14px", fontWeight: "500", color: "#333333" }}><b>{selectedETFs.length}</b> ETFs selected</div>
                    <div style={{ margin: "4% 2%", fontSize: "14px", fontWeight: "400", color: "#6c6c6c" }}>Weights (%)</div>
                    <BsThreeDotsVertical onClick={() => setShowMainModal(true)} size={26} style={{ color: '#333333', border: "0.54px solid #333", borderRadius: "50%", margin: "auto 0 auto 2%", padding: "2px" }} />
                </div>
                {
                    selectedETFs.map((fund, index) => {
                        return (
                            <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }} key={fund.UID}>
                                <div style={{ display: "flex" }}>
                                    <img src={MintboxSmallCube} style={{ width: "20px", height: "20px", padding: "2px", marginRight: "4%", border: "1px solid #e5e5e5", borderRadius: 5 }} />
                                    <div style={{ flex: "1" }}>
                                        <div style={{ color: "#333333", fontSize: "14px", fontWeight: "500" }}>{fund.ETFName}</div>
                                        <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>{"NAV  " + fund.NAV}</div>
                                    </div>
                                    {
                                        fund.isLocked ?
                                            <BsFillLockFill size={16} style={{ color: cssVar.getPropertyValue('--primary'), margin: "auto 1%" }} onClick={() => onLock(fund.UID)} />
                                            :
                                            <BsFillUnlockFill size={16} style={{ color: cssVar.getPropertyValue('--primary'), margin: "auto 1%" }} onClick={() => onLock(fund.UID)} />
                                    }
                                    <input type="tel" maxLength={2} value={fund.weight} style={{ width: "60px", height: "37px", margin: "auto 1%", textAlign: "center", border: "1px solid #e5e5e5", outline: "none", borderRadius: "4px", background: "#FAFAFA" }}
                                        onChange={e => assignWeights(index, parseInt(e.target.value))}
                                    />
                                    <BsThreeDotsVertical size={26} style={{ color: '#333333', border: "1px solid #333", borderRadius: "50%", margin: "auto 0 auto 2%", padding: "2px" }}
                                        onClick={() => {
                                            setSelectedModalFund(fund)
                                            setShowFundModal(true)
                                        }} />
                                </div>
                            </div>
                        )
                    })
                }
                <div style={{ padding: "2% 4%", textAlign: "center", borderBottom: "1px solid #e5e5e5" }} onClick={() => props.history.goBack()}>
                    Add more ETFs <BsPlusCircle size={20} style={{ background: "#F1FBF4", color: '#00A6A4', borderRadius: "50%" }} />
                </div>
                {selectedETFs.length === 1 && <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333" }}>Add at least <b>one more</b> ETF to build your own mintbox</div>}
                <Footer />
            </div>

            <MainModal />
            <FundModal />
            <HistoricalModal />
        </div>
    )
}

export default MintboxAllocation
