import React from 'react';

import InfiniteScroll from "react-infinite-scroll-component";
import { MintboxSmallCube } from "assets/SVG";

    import { BsPlusCircle } from 'react-icons/bs';
import cssVar from 'views/mobileView/styles';
import { FiSearch } from 'react-icons/fi'
import MainHeader from '../molecules/Headers/MainHeader';
import db from 'libs/db';
import AdminServices from 'services/api/AdminServices';
import { ArrayScrollLoader } from '../atoms/ArrayScrollLoader';
import { toast } from 'react-toastify';

const RECORDS_PER_FETCH = 20;

export default function SearchScreen ({ route, navigation, type = 1, onClick, queryForFilter = "", isOpen = () => { }, stpFundCode = "" }) {
   

    var textInp = React.createRef('')

    React.useEffect(() => {

        if (route.params.type === 3) {
            getMintboxes()
        } else if (route.params.type === 1) {
            getETFs()
        } else {
            getMF()
        }
    }, [])



    // const [finalDatalist, setdatalist] = React.useState([]);
    // const [showMore, setShowMore] =React.useState(true);

    // const [loadingMore, setLoadingMore] = React.useState(false);

    // const [showSortByModal, setShowSortByModal] = React.useState(false);
    // const [animateModal, setanimateModal] = React.useState(true);


    const [mainData, setmainData] = React.useState([]);
    const [data, setData] = React.useState([]);
    const [text, setText] = React.useState('');
    const [searchdata, setsearchData] = React.useState([]);
    const [loading, setloading] = React.useState(true);

    const fetchData = async (arr, text = '') => {
        const response = await ArrayScrollLoader(arr, 25, true);

        if (type) {
            if (response !== 'done') {

                setData(response);

            } else {
                setData([])
            }

        } else {
            if (text !== '') {
                if (response !== 'done') {

                    setData(response);

                } else {
                    setData([])
                }
            } else {
                setData([])

            }
        }


    };

    const loadMoreFunds = async () => {
        if (text === '') {
            const response = await ArrayScrollLoader(mainData, 10);

            if (response !== 'done') {

                setData([...data, ...response]);

            }
        } else {
            const response = await ArrayScrollLoader(searchdata, 10);
            if (response !== 'done') {
                setData([...data, ...response]);

            }
        }

    };

    const filterSearch = (text) => {
        setText(text)

        if (text.length > 0) {
            if (route.params.type === 3) {

                text = text.split(' ');
                var newData = mainData.filter(function (listItem) {
                    return text.every(function (el) {
                        return listItem.MintboxName.toLowerCase().indexOf(el.toLowerCase()) > -1;
                    });
                });
                setsearchData(newData);
                fetchData(newData, text)


            } else if (route.params.type === 1) {

                text = text.split(' ');
                var newData = mainData.filter(function (listItem) {
                    return text.every(function (el) {
                        return listItem.ETFName.toLowerCase().indexOf(el.toLowerCase()) > -1 || listItem.Symbol.toLowerCase().indexOf(el.toLowerCase()) > -1;
                    });
                });
                setsearchData(newData);
                fetchData(newData, text)


            } else {
                text = text.split(' ');
                var newData = mainData.filter(function (listItem) {
                    return text.every(function (el) {
                        return listItem.FundName.toLowerCase().indexOf(el.toLowerCase()) > -1;
                    });
                });
                setsearchData(newData);
                fetchData(newData, text)


            }
        } else {
            setsearchData([]);
            setData([])

        }


    };





    const getMintboxes = async () => {
        try {
            db.get(db.model.mintbox).then(async (p) => {
                const aa = p;
                if (p === null) {
                    db.get(db.model.user,true).then(async (ap) => {
                        // ap = JSON.parse(ap);
                        const res = await AdminServices.getAllMintboxes(ap.userData.id).then((r) => r);
                        setmainData(res.data)
                        fetchData(res.data).then(() => setloading(false));
                        db.set(db.model.mintbox, JSON.stringify(res.data))
                    });


                } else {
                    fetchData(aa).then(() => setloading(false));

                    setmainData(aa)
                }
            })
        } catch (error) {
            toast( 'Error Occured!!');

        }
    }

    const getETFs = async () => {
        try {
            db.get(db.model.etf).then(async (p) => {
                const aa = p;
                if (p === null) {
                    const res = await AdminServices.getAllETFs().then((r) => r);
                    setmainData(res.data)
                    fetchData(res.data).then(() => setloading(false));
                    db.set(db.model.etf, JSON.stringify(res.data))
                } else {
                    setmainData(aa)
                    fetchData(aa).then(() => setloading(false));

                }
            })
        } catch (error) {
            toast( 'Error Occured!!');
        }
    }

    const getMF = async () => {
        try {
            db.get(db.model.mfData).then(async (p) => {
                const aa = p;
                if (p === null) {
                    const res = await AdminServices.getFundsData().then((r) => r);
                    if (stpFundCode !== "") {

                        if (queryForFilter !== "") {
                            var finalData = res.data.filter(listItem => listItem.AMCName.toLowerCase() === queryForFilter.toLowerCase())
                            let obj = finalData.find(o => o.FundCode === Number(stpFundCode));

                            finalData.splice(finalData.indexOf(obj), 1)

                            setmainData(finalData)
                            fetchData(finalData).then(() => setloading(false));

                        } else {
                            setmainData(res.data)
                            fetchData(res.data).then(() => setloading(false));

                        }
                    } else {
                        if (queryForFilter !== "") {
                            setmainData(res.data.filter(listItem => listItem.AMCName.toLowerCase() === queryForFilter.toLowerCase()))
                            fetchData(res.data.filter(listItem => listItem.AMCName.toLowerCase() === queryForFilter.toLowerCase())).then(() => setloading(false));

                        } else {
                            setmainData(res.data)
                            fetchData(res.data).then(() => setloading(false));

                        }
                    }
                    db.set(db.model.mfData, JSON.stringify(res.data))
                } else {

                    if (stpFundCode !== "") {

                        if (queryForFilter !== "") {
                            var finalData = aa.filter(listItem => listItem.AMCName.toLowerCase() === queryForFilter.toLowerCase())
                            let obj = finalData.find(o => o.FundCode === Number(stpFundCode));

                            finalData.splice(finalData.indexOf(obj), 1)

                            setmainData(finalData)
                            fetchData(finalData).then(() => setloading(false));

                        } else {
                            setmainData(aa)
                            fetchData(aa).then(() => setloading(false));

                        }
                    } else {
                        if (queryForFilter !== "") {
                            setmainData(aa.filter(listItem => listItem.AMCName.toLowerCase() === queryForFilter.toLowerCase()))
                            fetchData(aa.filter(listItem => listItem.AMCName.toLowerCase() === queryForFilter.toLowerCase())).then(() => setloading(false));

                        } else {
                            setmainData(aa)
                            fetchData(aa).then(() => setloading(false));

                        }
                    }

                }
            })
        } catch (error) {
            toast( 'Error Occured!!');

        }
    }

    const letsnavigate = (data) => {
        route.params.type === 3 ? navigation.navigate('MintboxDetails', {

            routeData: data

        }

        ) : route.params.type === 1 ? navigation.navigate('ETFDetails', { routeData: data }) : navigation.navigate('FundDetails', { routeData: data })
    }

    const onItemPress = (item) => {
        textInp.current.blur()
        setText('')
        setData([])

        isOpen(false)

        type ? letsnavigate(item) :
            onClick({ ...item, type: route.params.type })

    }



    const filterList = () => {
        if (text.length > 3) {
            if (route.params.type === 3) {
                return data.filter(listItem => listItem.MintboxName.toLowerCase().includes(text.toLowerCase()));

            } else if (route.params.type === 1) {
                return data.filter(listItem => listItem.ETFName.toLowerCase().includes(text.toLowerCase()));
            } else {
                return data.filter(listItem => listItem.FundName.toLowerCase().includes(text.toLowerCase()));
            }
        } else {
            return []
        }

    }

    const changeText = (text) => {
        if (text.length > 0) {
            isOpen(true)
        } else {
            isOpen(false)
        }
        setText(text)
    }

    return (
        <div style={{ flex: 1}}>

            {type ? <MainHeader header={route.params.header} navigation={navigation}
                right="help" /> : null}

            <div style={{ minWidth: "95%" }}>
                <div style={{ padding: 4, display: "flex", flexDirection: "row", alignItems: "center",border:"1px solid "+cssVar.getPropertyValue("--divider"), borderRadius: '4px'}}>
                    <FiSearch size={15} style={{ margin: "0 4%" }} />
                    <input    placeholder={route.params.text}                             ref={textInp}
                    autoFocus style={{ fontSize: 15, color: cssVar.getPropertyValue("--text"), border:"none", outline: "none", width: "95%",marginTop:2 }} value={text}
                        onChange={(e) => { filterSearch(e.target.value) }}
                    />
                </div>
                {data.length === 0 ? <div>
                    {text.length === 0 ? null :
                        <span style={{ textAlign: "center", padding: 10 }}> NO RESULT FOUND!!</span>}
                </div> : null}



                <InfiniteScroll
                    dataLength={data.length}
                    next={() => {
                        loadMoreFunds()
                    }}
                    hasMore={true}
                    loader={<h4></h4>}
                >
                    {data.map((item, index) => (
                        <div
                            key={index + "ak"}
                            onClick={() => onItemPress(item)}
                            style={{ padding:5, borderWidth: 0.3, borderRadius: 1, borderColor: cssVar.getPropertyValue("--divider"), marginHorizontal: type ? 16 : 0 ,border:"1px solid"+cssVar.getPropertyValue("--divider")}}>
                            <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
                                <img src={MintboxSmallCube} style={{ width: "20px", height: "20px", padding: "2px", marginRight: "4%", backgroundColor: "#F2FBFB", borderRadius: 5 }} />
                                <span style={{ fontSize: 14, fontWeight: "500", paddingHorizontal: 10, flex: 1 }}>
                                    {route.params.type === 3 ? item.MintboxName : route.params.type === 1 ? item.ETFName : item.FundCode + item.FundName}
                                </span>
                                {!type ? <BsPlusCircle size={22} style={{ background: "#F1FBF4", color: '#00A6A4', borderRadius: "50%", margin: "auto 0 auto 2%" }}
                                />
                                    : null}
                            </div>
                        </div>
                    ))}
                </InfiniteScroll>


                {/* <FlatList
                    data={data}
                    removeClippedSubviews={true}
                    keyExtractor={(item, index) => String(index)}
                    renderItem={({ item, index }) => (
                        <div
                            onClick={() => onItemPress(item)}
                            style={{ paddingHorizontal: 16, paddingVertical: 10, borderWidth: 0.3, borderRadius: 1, borderColor: cssVar.getPropertyValue("--divider"), marginHorizontal: type ? 16 : 0 }}>
                            <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
                                <MintBoxCube />
                                <span style={{ fontSize: 14, fontWeight: "500", paddingHorizontal: 10, flex: 1 }} numberOfLines={2}>
                                    {route.params.type === 3 ? item.MintboxName : route.params.type === 1 ? item.ETFName : item.FundCode + item.FundName}
                                </span>
                                {!type ? <Icon type="ionicon" name="add-circle-outline" size={22} color={t.colors.primary} style={{ marginRight: 10 }} />
                                    : null}
                            </div>
                        </div>
                    )}
                    onEndReachedThreshold={0.5}
                    onEndReached={() => {
                        loadMoreFunds()
                    }}
                /> */}



            </div>
        </div>
    );
}

