import React from 'react';
import { Tab, Tabs } from "react-bootstrap";
import { useSwipeable } from 'react-swipeable'

export default function TabBar({ tabsObject }) {

  // Sample tabsObject = [
  //   { key: 1, title: 'Overview', view: Overview() },
  //   { key: 2, title: 'Constituents', view: <MintboxConstituents mintboxId={mintboxId} /> },
  //   { key: 3, title: 'Factsheet', view: Factsheet() },
  //   { key: 4, title: 'Fee', view: Fee() },
  // ];

  const [activeKey, setActiveKey] = React.useState(1);

  const handlers = useSwipeable({
    onSwipedLeft: (e) => handelLeftSwipe(),
    onSwipedRight: (e) => handelRightSwipe(),
    trackMouse: true,
    trackTouch: true,
    preventDefaultTouchmoveEvent: true
  })
  const handelLeftSwipe = () => {
    if (activeKey === tabsObject.length) {
      setActiveKey(1)
    }
    else setActiveKey(activeKey + 1)
  }
  const handelRightSwipe = () => {
    if (activeKey === 1) {
      setActiveKey(tabsObject.length)
    }
    else setActiveKey(activeKey - 1)
  }

  return (
    <div {...handlers}>
      <style type="text/css">
        {`
         ul.nav-tabs {
           display: flex;
           gap: 32px;
           padding-left: 4%;
           padding-right: 4%;
           border-bottom: 1px solid #e5e5e5;
         }
         li.nav-item{
           align-items: space-around;
           font-size: 14px;
           font-weight: 400;
         }
         .nav-link {
          color: #999999;
          padding-left: 0;
          padding-right: 0;
         }
         .nav-link.active{
           color: #333333 !important;
           border-bottom: 3px solid #333333 !important;
         }
        `}
      </style>
      <Tabs activeKey={activeKey} className="mb-3" onSelect={(k) => setActiveKey(k * 1)}>
        {tabsObject.map((t) => {
          return (<Tab key={t.key} eventKey={t.key} title={t.title}>
                {t.view}
          </Tab>)
        })}
      </Tabs >
      </div>
  );
}
