import db from 'libs/db';
import React from 'react';
import { RiRepeatFill } from 'react-icons/ri';
import { toast } from "react-toastify";
import { setCreateMintboxData } from 'redux/CreateMintbox/actions';
import AdminServices from 'services/api/AdminServices';
import SearchBar from 'views/mobileView/components/atoms/SearchBar';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import SearchScreen from 'views/mobileView/components/pages/SearchScreen';

const ReplaceETF = (props) => {
    const [loading, setloading] = React.useState(false);

    const [selectedETFs, setSelectedETFs] = React.useState([]);

    const fundToReplace = props.location.state?.fundToReplace;

    React.useEffect(async () => {
        await getSelectedETFs();
    }, []);

    const getSelectedETFs = async () => {
        try {
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    setSelectedETFs(u.selectedETFs)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };

    const saveData = async (arr) => {
        const dataFinal = {
            selectedETFs: arr
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            setCreateMintboxData(u);
        })
    }

    const onSearchItemClick = (replacementFund) => {
        if(selectedETFs.some(fund => fund.UID === replacementFund.UID)){
            toast.error("Fund already exists")
            return;
        }
        const newArr = selectedETFs.map((fund)=>{
            if(fund.UID === fundToReplace.UID){ 
                return {...replacementFund,weight:fundToReplace.weight,isLocked:fundToReplace.isLocked}
            }
            else return fund
        })
        saveData(newArr);
        props.history.goBack();
    }
    
    return (
        <div>
            <MainHeader
                header="Build your own mintbox"
                right="help"
                screenType="Invest"
                navigation={props.history}
            />
            <div className="containerData px-0 pb-4" style={{ marginBottom: "140px" }}>
                <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333", display: "flex", alignItems: "center",gap:"12px" }}>
                    <RiRepeatFill size={16}/>
                    <span style={{ flex: 1 }}>Replacing <b>'{fundToReplace.ETFName}'</b></span>
                </div>
                <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333"}}>Changed your mind?
                    <span style={{fontWeight: "500", color: "#00A6A4"}} onClick={()=>props.history.goBack()}> Continue building mintbox &gt;</span>
                </div>
                {/* <SearchBar type="ETF" allFunds={allETFs.filter(f=>!selectedETFs.some(x=>x.UID===f.UID))} showPlusIcon={true} onPlusIconClick={onPlusIconClick} /> */}
                <div style={{ margin: "4%" }}>
                    <SearchScreen route={{
                        params: {
                        text: 'Search by ETF name',
                        type: 1
                        }
                    }}
                    navigation={props.history}
                    type={0}
                    onClick={(item) => {onSearchItemClick(item)}}
                    />
                </div>
            </div>
        </div>
    )
}

export default ReplaceETF
