import { Button } from 'react-bootstrap';
import React from 'react';
import db from '../../../../../libs/db';
import { setUserData } from '../../../../../redux/CompleteProfile/actions';
import cssVar from 'views/mobileView/styles';

const Footer = ({
  next,
  navigation,
  state,
  nextDisabled = false,
  buttonText
}) => {

  const onNextPress = () => {
    if(!nextDisabled){
      navigation.push({ pathname: next, state })
    }
  };

  return (
    <div className='footer'>
      <button
        style={{
          backgroundColor: cssVar.getPropertyValue('--brandSecondary'),
          borderRadius: "6px",
          color: "#fff",
          fontWeight: 500,
          height: "44px",
          flex: 1,
          width: "100%",
          opacity: nextDisabled ? 0.4 : 1
        }}
        onClick={() => onNextPress()}
      >
        {buttonText}
      </button>
    </div>
  );
};

export default Footer;
