import { MintboxSmallCube } from 'assets/SVG';
import React, { useState } from 'react'
import AdminServices from 'services/api/AdminServices'
import MainHeader from "views/mobileView/components/molecules/Headers/MainHeader";
import cssVar from 'views/mobileView/styles';
import ReadMoreText from 'views/mobileView/components/atoms/ReadMoreText'
import { toast } from 'react-toastify';
import db from 'libs/db';
import CustomAccordion from 'views/mobileView/components/atoms/Accordion';
import { Chip } from '@mui/material';


function MutualFundDetail(props) {
    const fundId = props.match.params.FundId;

    const [fund, setFund] = useState({});
    const [peerReturns,setPeerReturns] = useState({})
    const [rollingReturns,setRollingReturns] = useState({})
    const [isWatchlisted, setIsWatchlisted] = useState(false);
    const [user, setUser] = useState({})

    const [loading, setLoading] = useState(false)
    React.useEffect(() => {
        loadData();
    }, [])

    const loadData = () => {
        try {
            db.get(db.model.user, true).then((p) => {
                setUser(p.userData)
                getWatchlist(p.userData.id)
                updateRecentlyViewed(p.userData.id);
                getMutualFundById();
                getPeerReturnsByFundCode();
                getRollingReturnsByFundCode();
            });
        } catch (error) {
            console.log(error)
        }
    }

    const getMutualFundById = async () => {
        try {
            let res = await AdminServices.getFundByFundCode(fundId).then(res => res)
            console.log("Get MF by id API", res.data)
            if (res.type) {
                setFund(res.data)
            }
            else {
                setFund([])
                toast.error("Could not find fund")
            }
        } catch (error) {
            toast.error(error)
        }
    }
    const getPeerReturnsByFundCode = async () => {
        try {
            let res = await AdminServices.getPeerReturnsByFundCode(fundId).then(res => res)
            console.log("Get Peer Returns by id API", res.data)
            if (res.type) {
                setPeerReturns(res.data)
            }
            else {
                setPeerReturns({})
                toast.error("error")
            }
        } catch (error) {
            toast.error(error)
        }
    }
    const getRollingReturnsByFundCode = async () => {
        try {
            let res = await AdminServices.getRollingReturnsByFundCode(fundId).then(res => res)
            console.log("Get Rolling Returns by id API", res.data)
            if (res.type) {
                setRollingReturns(res.data)
            }
            else {
                setRollingReturns({})
                toast.error("error")
            }
        } catch (error) {
            toast.error(error)
        }
    }
    const updateRecentlyViewed = async (id) => {
        const data = {
            "clientId": id,
            "assetType": 2,
            "assetId": fundId
        }
        let res = await AdminServices.updateRecentlyViewed(data).then(res => res)
        if (res.data) {
            if (res.data.Result === 'success') {
                console.log("Recently Viewed Updated")
            }
        }
    }

    const getWatchlist = async (id) => {
        try {
            db.get(db.model.watchlist).then(async (p) => {
                if (p === null) {
                    let res = await AdminServices.getWatchlist(id).then(res => res);
                    db.set(db.model.watchlist, JSON.stringify(res.data))
                    setIsWatchlisted(res.data.some(f => f.FundType === 2 && f.FundCode === Number(fundId)))
                } else {
                    setIsWatchlisted(p.some(f => f.FundType === 2 && f.FundCode === Number(fundId)))
                }
            });
        } catch (error) {
            toast.error(error);
        }
    }

    const updateWatchlist = async () => {
        const data = {
            "clientId": user.id,
            "fundType": 2,
            "fundCode": fundId,
            "isActive": isWatchlisted ? 0 : 1
        }
        try {
            let res = await AdminServices.updateWatchlist(data).then(res => res)
            if (res.type) {
                setIsWatchlisted(!isWatchlisted)
            }
        } catch (error) {
            console.log(error)
        }

    }


    const Summary = () => {
        return (
            <div style={{ background: "#F8F8F8", padding: "4%", display: "flex" }}>
                <div style={{ marginRight: "4%" }}>
                    <div style={{ width: "48px", height: "48px", padding: "8px", marginRight: "4%", backgroundColor: "#FFFFFF", borderRadius: 5, border: "1px solid #E5E5E5" }}>
                        <img src={MintboxSmallCube} style={{ width: "100%" }} />
                    </div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500" }}>{fund.SchemeName}</div>
                    <div style={{ display: "flex", marginTop: "4%" }}>
                        <div style={{ flex: 1 }}>
                            <div className="textCardHelper">1 year return</div>
                            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500", color: cssVar.getPropertyValue("--primary") }}>{fund.OneYrReturn} %</div>
                        </div>
                        <div style={{ flex: 1 }}>
                            <div className="textCardHelper">Category</div>
                            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "400" }}>{fund.SubCategoryName}</div>
                        </div>
                    </div>
                    <div style={{ display: "flex", marginTop: "4%" }}>
                        <div style={{ flex: 1 }}>
                            <div className="textCardHelper">NAV</div>
                            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "400" }}> {fund.NAV}</div>
                        </div>
                        <div style={{ flex: 1 }}>
                            <div className="textCardHelper">Min SIP</div>
                            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "400" }}> {fund.MinSIPInvestAmount}</div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    const CompWithPeers = () => {
        return (
            <>
                {Object.keys(peerReturns).length !== 0?
                <div style={{textAlign:"center"}}>
                    <div style={{display:"flex",paddingBottom:"14px"}}>
                        <div style={{flex:2}}></div>
                        <div style={{flex:1,fontSize:"12px",fontWeight:"400",color:"#6c6c6c"}}>6M</div>
                        <div style={{flex:1,fontSize:"12px",fontWeight:"400",color:"#6c6c6c"}}>1Y</div>
                        <div style={{flex:1,fontSize:"12px",fontWeight:"400",color:"#6c6c6c"}}>3Y</div>
                        <div style={{flex:1,fontSize:"12px",fontWeight:"400",color:"#6c6c6c"}}>5Y</div>
                    </div>
                    <div style={{display:"flex",paddingBottom:"14px"}}>
                        <div style={{flex:2, fontSize:"14px",fontWeight:"400",color:"#00A6A4",textAlign:"left"}}>Mutual fund</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Fund6month.toFixed(1)}%</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Fund1year.toFixed(1)}%</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Fund3year.toFixed(1)}%</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Fund5year.toFixed(1)}%</div>
                    </div>
                    <div style={{display:"flex",paddingBottom:"14px"}}>
                        <div style={{flex:2, fontSize:"14px",fontWeight:"400",color:"#6c6c6c",textAlign:"left"}}>Category</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Cat6month.toFixed(1)}%</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Cat1year.toFixed(1)}%</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Cat3year.toFixed(1)}%</div>
                        <div style={{flex:1, fontSize:"14px",fontWeight:"400",color:"#333333"}}>{peerReturns.Cat5year.toFixed(1)}%</div>
                    </div>
                </div>
                :''
                }
            </>
            )
        }
    const AUM = () => {
        return (<>
            <div style={{ display: "flex",padding:"2% 0", borderBottom: "1px solid #e5e5e5" }}>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>AUM</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}> {fund.AUM} crore</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Fund manager</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}>{fund.FundManager}</div>
                </div>
            </div>
            <div style={{ display: "flex",padding:"2% 0",alignItems:"center" }}>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Expense ratio</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}>{fund.ExpenseRatio} %</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div>
                        <button style={{ fontSize: "12px", fontWeight: "500", color: "#00A6A4", border: "1px solid #00A6A4", borderRadius: "6px", display: "flex", width:"75%",background:"transparent",padding:"2% 4%",textAlign:"left" }}>
                            <div style={{ flex: 1 }}>Explore low-cost index funds</div>
                            <span style={{color:"#00A6A4",fontSize:"22px", margin:"auto 0 auto 5px"}}>&gt;</span>
                        </button>
                    </div>
                </div>
            </div>
        </>
        )
    }
    const SIPLumpsum = () => {
        return (
            <div style={{ height: "50px" }}>
                Graph
            </div>
        )
    }
    const RollingReturns = () => {
        const chipset = [
            {key:1,name:"1 year"},
            {key:3,name:"3 years"},
            {key:5,name:"5 years"},
            {key:10,name:"10 years"},
        ]
        const [duration,setDuration] = React.useState(1);
        const [graphArray,setGraphArray] = React.useState([]);
        const [avgRR,setAvgRR]=React.useState();
        const [sd,setSd]=React.useState();

        React.useEffect(()=>{
            switch(duration){
                case 1: setGraphArray(rollingReturns.OneYrRRData);
                        setAvgRR(rollingReturns.OneYrAvgRR.toFixed(2));
                        setSd(rollingReturns.OneYrSD.toFixed(2));
                        return;
                case 3: setGraphArray(rollingReturns.ThreeYrRRData);
                        setAvgRR(rollingReturns.ThreeYrAvgRR.toFixed(2));
                        setSd(rollingReturns.ThreeYrSD.toFixed(2));
                        return;
                case 5: setGraphArray(rollingReturns.FiveYrRRData);
                        setAvgRR(rollingReturns.FiveYrAvgRR.toFixed(2));
                        setSd(rollingReturns.FiveYrSD.toFixed(2));
                        return;
                case 10: setGraphArray(rollingReturns.TenYrRRData);
                        setAvgRR(rollingReturns.TenYrAvgRR.toFixed(2));
                        setSd(rollingReturns.TenYrSD.toFixed(2));
                        return;
                default:setGraphArray([]);
                        setAvgRR(0);
                        setSd(0);
            }
        },[duration])
        return (
            <>
                {chipset.map((item) => {
                    return (
                        <Chip
                            className={duration===item.key?"chip-a selected":"chip-a"}
                            key={item.key}
                            label={item.name}
                            onClick={() => {
                                setDuration(item.key)
                            }}
                        />
                    )
                })}
                <div style={{fontSize:"12px",fontWeight:"400",fontColor:"#6c6c6c",paddingTop:"4%"}}>Avg rolling return<span style={{fontSize:"14px",fontWeight:"500",color:"#F5A447"}}> {avgRR}%</span></div>
                <div style={{fontSize:"12px",fontWeight:"400",fontColor:"#6c6c6c"}}>Standard deviation<span style={{fontSize:"14px",fontWeight:"500",color:"red"}}> {sd}%</span></div>
            </>
        )
    }
    const MinSubscription = () => {
        return (<>
            <div style={{ display: "flex",padding:"2% 0", borderBottom: "1px solid #e5e5e5" }}>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Minimum SIP</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}> {fund.MinSIPInvestAmount}</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Minimum lumpsum</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}> {fund.MinLumpsumInvestAmount}</div>
                </div>
            </div>
            <div style={{ display: "flex",padding:"2% 0" }}>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Minimum redemption</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}> {fund.MinRedemptionAmount}</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Minimum additional</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}> {fund.MinSubsequentAmount}</div>
                </div>
            </div>
        </>
        )
    }
    const TopHolding = () => {
        const [selection,setSelection] = useState(0)
        return (<>
            <div style={{ display: "flex",padding:"2% 0", borderBottom: "1px solid #e5e5e5",alignItems:"end" }}>
                <div style={{ flex: 2 }}>
                    <Chip
                        className={selection===0?"chip-a selected":"chip-a"}
                        label="Companies"
                        onClick={() => {
                            setSelection(0)
                        }}
                    />
                    <Chip
                        className={selection===1?"chip-a selected":"chip-a"}
                        label="Sectors"
                        onClick={() => {
                            setSelection(1)
                        }}
                    />
                </div>
                <div style={{ flex: 1,color: "#6C6C6C", fontSize: "14px", fontWeight: "500",textAlign:"right" }}>
                    Allocation (%)
                </div>
            </div>
            <div style={{ display: "flex",padding:"2% 0",alignItems:"center" , borderBottom: "1px solid #e5e5e5"}}>
                <div style={{ flex: 2 }}>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}>Reliance Industries Limited</div>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Energy</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400",textAlign:'right' }}>50</div>
                </div>
            </div>
            <div style={{ display: "flex",padding:"2% 0",alignItems:"center" , borderBottom: "1px solid #e5e5e5"}}>
                <div style={{ flex: 2 }}>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}>Reliance Industries Limited</div>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Energy</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400",textAlign:'right' }}>50</div>
                </div>
            </div>
            <div style={{ display: "flex",padding:"2% 0",alignItems:"center" }}>
                <div style={{ flex: 2 }}>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "500" }}>Total</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "500",textAlign:'right' }}>100</div>
                </div>
            </div>
        </>)
    }
    const ExitLoad = () => {
        return (<>
            <div style={{ display: "flex",padding:"2% 0", borderBottom: "1px solid #e5e5e5" }}>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Exit load</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}>1% of redeemed value within 365 days</div>
                    <div style={{ color: "#6c6c6c", fontSize: "12px", fontWeight: "400" }}>for units in excess of 10% of your investment value</div>
                </div>
            </div>
            <div style={{ display: "flex",padding:"2% 0" }}>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Lock-in</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}>No lock-in</div>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>Taxation</div>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "400" }}>{fund.TaxTreatment}</div>
                </div>
            </div>
        </>
        )
    }
    const accordionArray = [
        { key: "1", title: "Comparison with peers", body: CompWithPeers() },
        { key: "2", title: "AUM, fund manager and expense ratio", body: AUM() },
        { key: "3", title: "SIP and Lumpsum returns", body: SIPLumpsum() },
        { key: "4", title: "Rolling returns and standard deviation", body: RollingReturns() },
        { key: "5", title: "Minimum subscriptions", body: MinSubscription() },
        { key: "6", title: "Top holdings in the fund", body: TopHolding() },
        { key: "7", title: "Exit load, lock-in and taxation", body: ExitLoad() },
    ]
    const Footer = () => {
        return (
            <div className='bottom-nav' style={{ height: "70px" }}>
                <div style={{ flex: 1, padding: "4%" }}>
                    <button
                        style={{
                            borderRadius: 4,
                            fontSize: "14px",
                            backgroundColor: cssVar.getPropertyValue("--brandSecondary"),
                            color: "#FFFFFF",
                            height: "44px",
                            width: "100%",
                            border: "none"
                        }}
                        onClick={() => { }}
                    >
                        Invest in this fund
                    </button>
                </div>
            </div>
        )
    }

    return (
        <>
            <div style={{ flex: 1 }}>
                <MainHeader
                    header="Direct mutual fund"
                    isWatchlisted={isWatchlisted}
                    onWatchlistIconClick={() => { updateWatchlist() }}
                    right="bookmark-share"
                    navigation={props.history}
                />
                <div className="containerData px-0 pb-4" style={{ marginBottom: "70px" }}>
                    <Summary />
                    <CustomAccordion accordionArray={accordionArray} />
                    <button style={{ fontSize: "14px", fontWeight: "500", color: "#00A6A4", border: "1px solid #00A6A4", borderRadius: "6px",background:"transparent",margin:"4%",height:"44px"}}>
                        See all mutual funds &gt;
                    </button>
                </div>
                <Footer />
            </div>
        </>
    )
}

export default MutualFundDetail