import React from 'react';
import { Spinner } from 'react-bootstrap';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';
import Footer from './Footer';

export default function GoalDue(props) {

	const [goalDue, setGoalDue] = React.useState("8");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "10", "name": "10 years" },
		{ "key": "12", "name": "12 years" },
		{ "key": "6", "name": "6 years" }
	]

	const checkValidation = () => {
		if (goalDue!=='0') {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	},[goalDue])

	return (
		<div>
			<MainHeader
				header="Plan child education"
				right="Home"
				screenType="goalScreen"
				navigation={props.history}
			/>
			<ProgressBar now={40} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">In how many years is the goal due?</div>
							<input
								type="tel"
								id="goalDue"
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='8 years'
								value={goalDue}
								onChange={(num) => {
									const a = Number(num.target.value.replace(/\D/g, '')).toString()
									setGoalDue(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row',overflowX:'auto',paddingBottom:'10px' }}>
								{chipset.map((item) => {
									return (
										<Chip
											className={goalDue===item.key?"chip-b selected":"chip-b"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setGoalDue(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/financial-goal/invest-today"
				state={{...props.location.state,goalDue}}
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
