import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import SearchBar from 'views/mobileView/components/atoms/SearchBar';
import SearchScreen from 'views/mobileView/components/pages/SearchScreen';

export default function FundSearch(props) {

  let type = props.match.params.type;
  type = type === 'ETFs' ? 1 : type === 'mutual-funds' ? 2 : 3;

  const onSearchResultClick = (item) => {
    console.log(item)
    if(type===1){
      props.history.push("/ETF-detail/"+item.UID)
    } else if(type===2){
      props.history.push("/fund-detail/"+item.FundCode)
    } else {
      props.history.push("/mintbox-detail/"+item.MintboxId)
    }
  }


  return (
    <div>
      <MainHeader
        header={type === 1 ? 'Invest in low cost ETFs' : type === 2 ? 'Invest in direct mutual funds' : 'Invest in mintboxes'}
        right="help"
        screenType="Invest"
        navigation={props.history}
      />
      <div className="containerData px-0 pb-4">
        <div style={{margin:"4%"}}>
          <SearchScreen route={{
            params: {
              text: type === 1 ? 'Search by ETF name' : type === 2 ? 'Search by mutual fund name' : 'Search by mintbox name',
              type: type
            }
          }}
          navigation={props.history}
          type={0}
          onClick={(item) => {onSearchResultClick(item)}}
          /> 
        </div>
      </div>
    </div>
  )
}
