import React from "react";
import { Redirect, Route } from "react-router-dom";

function AdminProtectedRoutes({ component: Component, ...restOfProps }) {

    const cyrb53 = function(str, seed = 0) {
        let h1 = 0xdeadbeef ^ seed, h2 = 0x41c6ce57 ^ seed;
        for (let i = 0, ch; i < str.length; i++) {
            ch = str.charCodeAt(i);
            h1 = Math.imul(h1 ^ ch, 2654435761);
            h2 = Math.imul(h2 ^ ch, 1597334677);
        }
        h1 = Math.imul(h1 ^ (h1>>>16), 2246822507) ^ Math.imul(h2 ^ (h2>>>13), 3266489909);
        h2 = Math.imul(h2 ^ (h2>>>16), 2246822507) ^ Math.imul(h1 ^ (h1>>>13), 3266489909);
        return 4294967296 * (2097151 & h2) + (h1>>>0);
    };

    console.log(cyrb53("yesitisamintboxadmin"))
    console.log(cyrb53("yesitisamintboxadmin"))

    const isAuthenticated = localStorage.getItem("isAdmin");
    console.log("HEYY",isAuthenticated == cyrb53("yesitisamintboxadmin"))
    return (
        <Route
            {...restOfProps}
            render={(props) =>
                isAuthenticated == cyrb53("yesitisamintboxadmin") ? <Component {...props} /> : <Redirect to="/lo-admin" />
            }
        />
    );
}

export default AdminProtectedRoutes;
