import React from 'react';
import { Spinner } from 'react-bootstrap';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';
import Footer from './Footer';

export default function MonthlyContri(props) {

	const [name, setName] = React.useState("");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const checkValidation = () => {
		if (name.length>1) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	},[name])

	return (
		<div>
			<MainHeader
				header="Plan child education"
				right="Home"
				screenType="goalScreen"
				navigation={props.history}
			/>
			<ProgressBar now={100} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">Name this goal</div>
							<input
								type="text"
								id="name"
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='Education'
								value={name}
								onChange={(n) => {
									setName(n.target.value);
								}}
							/>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/financial-goal/name"
				state={{...props.location.state,name}}
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
