import mintbox_cube from 'assets/SVG/mintbox-cube.svg';
import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { BiRupee } from 'react-icons/bi';
import { FiExternalLink } from 'react-icons/fi';
import TabBar from '../../../../components/atoms/TabBar';
import MainHeader from '../../../../components/molecules/Headers/MainHeader';
import cssVar from '../../../../styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import { ActivateDirectMFImg, MintboxSmallCube } from 'assets/SVG';
import AdminServices from 'services/api/AdminServices';
import db from 'libs/db';
import { formatDate } from 'views/mobileView/components/atoms/DateFormatter';
import { toast } from "react-toastify";
import { dividerClasses } from '@mui/material';

export default function WatchlistScreen(props) {
  const [user,setUser]=React.useState()
  const [watchlist,setWatchlist] = React.useState([])
  const [loading,setLoading] = React.useState()
  React.useEffect(()=>{
    loadData();
  },[])

  const loadData = () => {
    try {
      db.get(db.model.user,true).then((p) => {
        setUser(p.userData)
        getWatchlist(p.userData.id)
      });
    } catch (error) {
      console.log(error)
    }
  }

  const getWatchlist = async (id) => {
    try {
      setLoading(true);
      db.get(db.model.watchlist).then(async (p) => {
        console.log(p)
        if (p === null) {
          let res = await AdminServices.getWatchlist(id).then(res => res);
          db.set(db.model.watchlist, JSON.stringify(res.data))
          setWatchlist(res.data)
          setLoading(false)
        } else {
          setWatchlist(p)
          setLoading(false)
        }
      });
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  }

  const MintboxCard = (mintbox) => {
    return (
      <div key={mintbox.FundCode} style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }} onClick={() => { props.history.push("/mintbox-detail/" + mintbox.FundCode) }}>
        <div style={{ display: "flex" }}>
          <div style={{ width: "48px", height: "48px", padding: "8px", marginRight: "4%", backgroundColor: "#F2FBFB", borderRadius: 5 }}>
            <img src={MintboxSmallCube} style={{ width: "100%" }} />
          </div>
          <div style={{ flex: "1" }}>
            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500" }}>{mintbox.FundName}</div>
            <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight") }}>Watchlisted on {formatDate(mintbox.CreatedOn)}</p>
          </div>
        </div>
        <div>
          <p style={{ color: "#6c6c6c", fontSize: "14px", fontWeight: "400" }}>Current value of <span className='underLineBlue'>1000</span> invested on 1st Jan 2010...</p>
        </div>
        <div style={{ display: "flex"}}>
          <div style={{ flex: 1 }}>
            <div className="textCardHelper" style={{color: cssVar.getPropertyValue("--primary") }}>mintbox</div>
            <span style={{fontSize:"16px",fontWeight:'400',color:"#333333"}}> 1,058</span>
            <span style={{fontSize:"12px",fontWeight:'500', color: cssVar.getPropertyValue("--primary") }}> (5.67%)</span>
          </div>
          <div style={{ flex: 1 }}>
            <div className="textCardHelper" style={{color: '#F3B92A' }}>NIFTY 50</div>
            <span style={{fontSize:"16px",fontWeight:'400',color:"#333333"}}> 920</span>
            <span style={{fontSize:"12px",fontWeight:'500', color: 'red' }}> (-8.34%)</span>
          </div>
        </div>
      </div>
    )
  }

  const MintboxView = () => {
    const mintboxArray = watchlist.filter(f=>f.FundType===3)
    return (
      <>
        {mintboxArray.length === 0 ?
          <div className='containerData mb-4'>
            <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
              <img src={ActivateDirectMFImg} width="50%" style={{ margin: "4% auto" }} />
              <div style={{ fontSize: "18px", fontWeight: "500", margin: "4% 0%" }}>You are not tracking any mintboxes yet.</div>
              <button onClick={()=>props.history.push('/list-mintboxes')} className='footer-button' style={{ color: "#294c88", border: "1px solid #294c88", backgroundColor: "transparent" }}>Add mintboxes to track</button>
            </div>
          </div>
          :
          <div>
            {
              mintboxArray.map(mintbox=> MintboxCard(mintbox))
            }
            <div className='footer'>
              <button onClick={()=>props.history.push('/list-mintboxes')} className='footer-button' style={{ color: "#294c88", border: "1px solid #294c88", backgroundColor: "transparent" }}>Add more mintboxes to track</button>
            </div>
          </div>
        }
      </>
    )
  }
  const ETFView = () => {
    const etfArray = watchlist.filter(f=>f.FundType===1)
    return (
      <>
      {etfArray.length === 0 ?
        <div className='containerData mb-4'>
          <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
            <img src={ActivateDirectMFImg} width="50%" style={{ margin: "4% auto" }} />
            <div style={{ fontSize: "18px", fontWeight: "500", margin: "4% 0%" }}>You are not tracking any ETFs yet.</div>
            <button onClick={()=>props.history.push('/list/ETFs')} className='footer-button' style={{ color: "#294c88", border: "1px solid #294c88", backgroundColor: "transparent" }}>Add ETFs to track</button>
          </div>
        </div>
        :
        <div>
          {
            etfArray.map(mintbox=> <div>{mintbox.FundName}</div>)
          }
          <div className='footer'>
            <button onClick={()=>props.history.push('/list/ETFs')} className='footer-button' style={{ color: "#294c88", border: "1px solid #294c88", backgroundColor: "transparent" }}>Add more ETFs to track</button>
          </div>
        </div>
      }
    </>
    )
  }

  const MFView = () => {
    const mfArray = watchlist.filter(f=>f.FundType===2)

    return (
      <>
      {mfArray.length === 0 ?
        <div className='containerData mb-4'>
          <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
            <img src={ActivateDirectMFImg} width="50%" style={{ margin: "4% auto" }} />
            <div style={{ fontSize: "18px", fontWeight: "500", margin: "4% 0%" }}>You are not tracking any mutual funds yet.</div>
            <button onClick={()=>props.history.push('/list/mutual-funds')} className='footer-button' style={{ color: "#294c88", border: "1px solid #294c88", backgroundColor: "transparent" }}>Add mutual funds to track</button>
          </div>
        </div>
        :
        <div>
          {
            mfArray.map(mintbox=> <div>{mintbox.FundName}</div>)
          }
          <div className='footer'>
            <button onClick={()=>props.history.push('/list/mutual-funds')} className='footer-button' style={{ color: "#294c88", border: "1px solid #294c88", backgroundColor: "transparent" }}>Add more mutual funds to track</button>
          </div>
        </div>
      }
    </>
    )
  }

  const tabsObject = [
    { key: 1, title: 'Mintboxes', view: MintboxView() },
    { key: 2, title: 'ETFs', view: ETFView() },
    { key: 3, title: 'Mutual funds', view: MFView() },
  ];

  return (
    <div>
      <MainHeader header="Watchlist" navigation={props.history} />
      {
        loading?
        <center>
          <Spinner
            as="span"
            animation="grow"
            size="sm"
            role="status"
            aria-hidden="true" />
        </center>
        :
        <div className='mx-0 pb-4' style={{marginBottom:"70px"}}>
          <Tabs tabsObject={tabsObject} />
        </div>
      }
    </div>
  )
}