import { MintboxSmallCube } from 'assets/SVG';
import React, { useState } from 'react'
import AdminServices from 'services/api/AdminServices'
import Tabs from 'views/mobileView/components/atoms/Tabs';
import MainHeader from "views/mobileView/components/molecules/Headers/MainHeader";
import cssVar from 'views/mobileView/styles';
import ReadMoreText from 'views/mobileView/components/atoms/ReadMoreText'
import { BsDownload } from 'react-icons/bs';
import { AiFillPlayCircle } from 'react-icons/ai';
import MintboxConstituents from './MintboxConstituents';
import { toast } from 'react-toastify';
import db from 'libs/db';


function MintboxDetail(props) {
  const mintboxId = props.match.params.MintboxId;

  const [mintbox, setMintbox] = useState({});
  const [isWatchlisted,setIsWatchlisted] = useState(false);
  const [watchlist,setWatchlist] = useState([])
  const [user, setUser] = useState({})

  React.useEffect(()=>{
    loadData();
  },[])

  React.useEffect(()=>{
    console.log("isWatchlistedddddddddddddddddddddddddddddddddddddddddd",isWatchlisted) 
  },[isWatchlisted])

  const loadData = () => {
    try {
      db.get(db.model.user,true).then((p) => {
        setUser(p.userData)
        getWatchlist(p.userData.id)
        updateRecentlyViewed(p.userData.id);
        getMintboxById();
      });
    } catch (error) {
      console.log(error)
    }
  }

  const getMintboxById = async () => {
    let res = await AdminServices.getMintboxById(mintboxId).then((res) => { return res })
    console.log("Get Mintbox by id API", res.data)
    setMintbox(res.data[0])
  }

  const updateRecentlyViewed = async (id) => {
    const data = {
      "clientId": id,
      "assetType": 3,
      "assetId": mintboxId
    }
    let res = await AdminServices.updateRecentlyViewed(data).then(res => res )
    if (res.data) {
      if (res.data.Result === 'success') {
        console.log("Recently Viewed Updated")
      }
    }
  }

  const getWatchlist = async (id) => {
    try {
      db.get(db.model.watchlist).then(async (p) => {
        console.log(p)
        if (p === null) {
          let res = await AdminServices.getWatchlist(id).then(res => res);
          db.set(db.model.watchlist, JSON.stringify(res.data))
          setWatchlist(res.data)
          setIsWatchlisted(res.data.some(f=>f.FundType===3 && f.FundCode===Number(mintboxId)))
        } else {
          setWatchlist(p)
          setIsWatchlisted(p.some(f=>f.FundType===3 && f.FundCode===Number(mintboxId)))
        }
      });
    } catch (error) {
      toast.error(error);
    }
  }

  const updateWatchlist = async () => {
    const data = {
        "clientId": user.id,
        "fundType": 3,
        "fundCode": mintboxId,
        "isActive": isWatchlisted? 0 : 1
    }
    console.log("updateWatchlist",data)
    try {
      let res = await AdminServices.updateWatchlist(data).then(res => res)
      if(res.type){
        setIsWatchlisted(!isWatchlisted)
      }
    } catch (error) {
        console.log(error)
    }

}


  const Summary = () => {
    return (
      <div style={{ background: "#F8F8F8", padding: "4%", display: "flex" }}>
        <div style={{ marginRight: "4%" }}>
          <div style={{ width: "48px", height: "48px", padding: "8px", marginRight: "4%", backgroundColor: "#F2FBFB", borderRadius: 5, border: "1px solid #E5E5E5" }}>
            <img src={MintboxSmallCube} style={{ width: "100%" }} />
          </div>
        </div>
        <div style={{ flex: 1 }}>
          <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500" }}>{mintbox.MintboxName}<br />{mintbox.MintboxName}</div>
          <div style={{ display: "flex", marginTop: "4%" }}>
            <div style={{ flex: 1 }}>
              <div className="textCardHelper">CAGR</div>
              <div className="textNormal" style={{ color: cssVar.getPropertyValue("--primary") }}>{mintbox.XIRR} %</div>
            </div>
            <div style={{ flex: 1 }}>
              <div className="textCardHelper">Risk level</div>
              <div className="textNormal">{mintbox.RiskLevel}</div>
            </div>
          </div>
          <div style={{ display: "flex", marginTop: "4%" }}>
            <div style={{ flex: 1 }}>
              <div className="textCardHelper">Ideal for</div>
              <div className="textNormal">&lt; 1 year</div>
            </div>
            <div style={{ flex: 1 }}>
              <div className="textCardHelper">Min. investment</div>
              <div className="textNormal">{mintbox.MinSIPAmount}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
  const Footer = () => {
    return (
      <div className='bottom-nav' style={{ height: "auto" }}>
        <div style={{ paddingLeft: "4%" }}>
          <div style={{ fontSize: "14px", fontWeight: "400", marginBottom: "2%" }}>Understand this mintbox</div>
          <div style={{ fontSize: "12px", fontWeight: "500", color: cssVar.getPropertyValue("--primary") }}>Watch explainer video &gt;</div>
        </div>
        <div style={{ flex: 1, padding: "4%" }}>
          <button
            style={{
              borderRadius: 4,
              fontSize: "14px",
              backgroundColor: cssVar.getPropertyValue("--brandSecondary"),
              color: "#FFFFFF",
              height: "44px",
              width: "100%",
              border: "none"
            }}
            onClick={() => { }}
          >
            Invest Now
          </button>
        </div>
      </div>
    )
  }
  const Overview = () => {
    return (
      <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }}>
        <ReadMoreText text="This is a short description of super portfolio1. We will talk about the logic of the strategy abc defgh ijklm nopqrstuvwxyz" />
      </div>
    )
  }
  const Fee = () => {
    return (
      <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }}>
        {
          mintbox.Fee !== 'test' ?
            <>
              <div style={{ width: "100%", height: '165px', background: "#F4F8FF", border: "1px solid #e5e5e5", borderRadius: "6px" }}></div>
              <div style={{ textAlign: "center", fontWeight: "500", fontSize: "16px", margin: '4%' }}>This is a free mintbox! </div>
              <div style={{ fontSize: "12px", color: "#6C6C6C", fontWeight: "400" }}><span style={{ fontWeight: "700", fontSize: "12px" }}>Disclaimer:</span> You may still have to pay third party charges like ETF expense ratio, brokerage, STT, capital gains tax etc.</div>
            </>
            :
            <>
              <p>This mintbox is available only for Pro and Black plan subscribers.</p>
              <div style={{ display: "flex", background: "#FAFAFA", padding: "4% ", marginBottom: "6%", border: "1px solid #e5e5e5", borderRadius: "6px" }}>
                <div style={{ flex: 1 }}>
                  <div style={{ fontSize: "14px", fontWeight: "500", marginBottom: "2%" }}>Annual fee (Pro plan)</div>
                  <div style={{ fontSize: "12px", fontWeight: "500", color: cssVar.getPropertyValue("--brandSecondary") }}>See features &gt;</div>
                </div>
                <div style={{ flex: 1, textAlign: "right" }}>
                  <div style={{ fontSize: "16px", fontWeight: "500", marginBottom: "2%" }}>0.5%</div>
                  <div style={{ fontSize: "12px", fontWeight: "400", color: cssVar.getPropertyValue("--textLight") }}>of portfolio value per year</div>
                </div>
              </div>
              <div style={{ display: "flex", background: "linear-gradient(to right,#202020,#014040)", padding: "4% ", marginBottom: "6%", borderRadius: "6px" }}>
                <div style={{ flex: 1 }}>
                  <div style={{ fontSize: "14px", fontWeight: "500", marginBottom: "2%", color: "#FFF" }}>Annual fee (Pro plan)</div>
                  <div style={{ fontSize: "12px", fontWeight: "500", color: "#98BBA3" }}>See features &gt;</div>
                </div>
                <div style={{ flex: 1, textAlign: "right" }}>
                  <div style={{ fontSize: "16px", fontWeight: "500", marginBottom: "2%", color: "#FFF" }}>0.4%</div>
                  <div style={{ fontSize: "12px", fontWeight: "400", color: "#98BBA3" }}>of portfolio value per year</div>
                </div>
              </div>
              <div style={{ color: cssVar.getPropertyValue('--primary'), fontSize: "14px", fontWeight: "500" }}><AiFillPlayCircle size={24} /> Watch how fee is charged</div>
            </>
        }
      </div>
    )
  }
  const Factsheet = () => {
    return (
      <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }}>
        <button style={{
          color: cssVar.getPropertyValue("--primary"),
          background: "#fff",
          border: "1px solid " + cssVar.getPropertyValue("--primary"),
          width: "100%",
          height: "44px",
          borderRadius: "6px"
        }}>Download Factsheet <BsDownload /></button>
        <div>
          <p style={{ fontWeight: "500", marginTop: "4%", marginBottom: "0" }}>What does the mintbox factsheet include?</p>
          <ul style={{ color: "#6C6C6C", fontSize: "14px" }}>
            <li>Overview</li>
            <li>Rebalancing Logic</li>
            <li>Research</li>
            <li>Constituents</li>
            <li>Fee</li>
            <li>Past performance data</li>
            <li>Risks and disclaimers</li>
          </ul>
        </div>
      </div>
    )
  }
  const tabsObject = [
    { key: 1, title: 'Overview', view: Overview() },
    { key: 2, title: 'Constituents', view: <MintboxConstituents mintboxId={mintboxId} /> },
    { key: 3, title: 'Fee', view: Fee() },
    { key: 4, title: 'Factsheet', view: Factsheet() }
  ];
  return (
    <>
      <div style={{ flex: 1 }}>
        <MainHeader
          header="Mintbox"
          isWatchlisted={isWatchlisted}
          onWatchlistIconClick={()=>{updateWatchlist()}}
          right="bookmark-share"
          navigation={props.history}
        />
        <div className="containerData px-0 pb-4 mb-5">
          <Summary />
          <Tabs tabsObject={tabsObject} />
        </div>
        <Footer />
      </div>
    </>
  )
}

export default MintboxDetail