import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';
import AdminServices from 'services/api/AdminServices';

export default function WillingnessPageResult(props) {

	const [riskWillingness,setRiskWillingness]=React.useState();

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const riskText = ['very low','low','moderate','high','aggressive']
	React.useEffect(()=>{
		loadData()
	},[])

	const loadData = async () => {
		setLoading(true);
		db.get(db.model.user, true).then((p) => {
			getRiskWillingness(p.userData.id)
		})	
	}

	const getRiskWillingness = async (id) => {
		const data = {
			"clientId":	id,
			"currentAge": Number(props.location.state.age),
			"noDependents": Number(props.location.state.dependents || 0),
			"annualIncome": Number(props.location.state.income || 0),
			"emergencyFund": Number(props.location.state.emergencyFund || 0),
			"insuranceCover": Number(props.location.state.lifeInsurance || 0),
			"isDependentInsured": Number(props.location.state.healthInsurance || 0),
			"portfolioPreference": Number(props.location.state.willingnessQuestion1),
			"planofAction": Number(props.location.state.willingnessQuestion2),
		}
		console.log(data)
		let res = await AdminServices.getRiskScore(data).then((res) => JSON.stringify(res));
		res = await JSON.parse(res);
		console.log("Get Risk API: ", res.data)
		if (res.type === true) {
			setRiskWillingness(res.data.RiskWillingness)
			setLoading(false)
			setNextDisabled(false)
		}
	}

	return (
		<div>
			<MainHeader
				header="Risk taking willingness"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={90} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
						  <div className='textHead' style={{fontWeight:"500"}}>We have identified your risk taking willingness as
						  <span className='textHead' style={{color:'red'}}> {riskText[riskWillingness-1]}</span></div>
						  <div className='textHead' style={{marginTop:"6%"}}>Please continue to the last step of selecting your investment horizon for wealth creation</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/investment-horizon"
				state={{...props.location.state,riskWillingness}}
				buttonText="Select investment horizon"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
