import db from "libs/db";
import React from "react";
import { toast } from "react-toastify";
import AdminServices from "services/api/AdminServices";
import TableMain from "views/admin/components/TableMain";
import cssVar from "views/mobileView/styles";


export default function ManageMintboxes(props) {

    const [allMintboxes, setAllMintboxes] = React.useState([]);
    React.useEffect(() => {
        loadData();
    }, []);

    const loadData = async () => {
        getAllMintbox();
    }

    const getAllMintbox = async () => {


        try {

            let res = await AdminServices.getAllMintboxes(-1).then((res) => JSON.stringify(res));
            res = await JSON.parse(res);
            console.log("Get All Mintboxes API: ", res.data)
            if (res.type === true) {
                setAllMintboxes(res.data)
            }
        } catch (error) {
            toast.error("ERROR : ", error)
        }



    }


    var actions = ['View/Edit', 'Delete']

    // props.history.push({ 
    //     pathname: "/mint-admin/mintbox/edit/"+rowData.MintboxId, 
    //     query: rowData
    //   })

    const actionClicked = (index, rowData) => {
        switch (index) {
            case 0:
              props.history.push("/mint-admin/mintbox/edit/"+rowData.MintboxId)
              break;
            case 1:
              alert("DELETE" + rowData.MintboxName)
              break;
            default:
              console.log("HEYE")
          }
    }


    return <div>

        <TableMain
            navigation={props.history}
            data={allMintboxes}
            others={actions}
            notRequired={['About']}
            header="All Mintboxes"
            onActionClick={(i,r) => actionClicked(i,r)}
        />
    </div>
}
