// @material-ui/core
import { MintboxSmallCube } from "assets/SVG";
import db from "libs/db";
import React from "react";
import { BiMinusCircle } from 'react-icons/bi';
import { BsFillLockFill, BsFillUnlockFill } from 'react-icons/bs';
import { toast } from "react-toastify";
import AdminServices from "services/api/AdminServices";
import { unique } from "views/mobileView/components/atoms/arrayObjUnique";
import SearchScreen from "views/mobileView/components/pages/SearchScreen";
import cssVar from "views/mobileView/styles";

export default function EditMintbox(props) {

    const [allETFs, setAllETFs] = React.useState([]);
    const [selectedEtf, setselectedEtf] = React.useState([]);

    const [loading, setloading] = React.useState(false);
    const [show, setshow] = React.useState(true);
    const [mintboxID, setMintboxID] = React.useState(props.match.params.mintboxID);


    const [name, setName] = React.useState('');
    const [clientID, setClientID] = React.useState('0');
    const [fee, setFee] = React.useState('');
    const [risk, setRisk] = React.useState(0);
    const [horizon, setHorizon] = React.useState(0);
    const [description, setDescription] = React.useState('');
    const [videoLink, setVideoLink] = React.useState('');
    const [isFixedAllo, setisFixedAllo] = React.useState('');
    const [rebalance, setRebalance] = React.useState(0);


    React.useEffect(() => {
        getMintboxById(props.match.params.mintboxID);
        getMintboxConstituentsById(props.match.params.mintboxID);
    }, [])
  
    const getMintboxConstituentsById = async (id) => {
      let res = await AdminServices.getMintboxConstituentsById(id).then((res) => { return res })
      console.log("Get mintbox constituents API", res.data)
    //   setMintboxConstituents(res.data)
    }
  
    
      const getMintboxById = async (id) => {
        let res = await AdminServices.getMintboxById(id).then((res) => { return res })
        console.log("Get Mintbox by id API", res.data)
        var mint = res.data[0]
         // setClientID(props.location.query.ClientId)
        // setFee(props.location.query.Fee)
        // setselectedEtf()
        // setRisk(props.location.query.Risk)
        // setHorizon(0)
        // setDescription(props.location.query.About)
        // setVideoLink()
        // setisFixedAllo()
        // setRebalance()
        // setName()
      }

    React.useEffect(async () => {


       
        // getAllETFs()
    }, []);

    React.useEffect(() => {
        console.log(selectedEtf)
    }, [selectedEtf]);


    const getAllETFs = async () => {
        try {
            console.log("start")
            await db.get(db.model.etf).then(async (u) => {
                console.log("u", u)
                if (u !== null) {
                    setAllETFs(u)
                    setloading(false)

                } else {
                    let res = await AdminServices.getAllETFs().then((res) => JSON.stringify(res));
                    res = await JSON.parse(res);
                    if (res.type === true) {
                        console.log("Get All ETFs API: ", res.data)
                        let newArr = res.data.map(fund => { return { ...fund, weight: '0', locked: false } })

                        console.log(newArr)

                        setAllETFs(newArr)
                        db.set(db.model.etf, JSON.stringify(newArr))
                        setloading(false)

                    }
                }
            })
        } catch (error) {
            toast.error(error);
            console.log("HEYEYEYE", error)
            setloading(false)

        }
    }

    const allocateETF = (item = {}) => {
        setselectedEtf([])

        var a = unique([...selectedEtf, item], "UID")

        if (100 % a.length === 0) {
            a = a.map((obj, index) => ({ ...obj, allocation: Number((100 / a.length).toFixed(0)), locked: false, index }))
            setselectedEtf(a)

        } else {
            a = a.map((obj, index) => ({ ...obj, allocation: Number((100 / a.length).toFixed(0)), locked: false, index }))
            a[0].allocation = Number(a[0].allocation) + 1
            setselectedEtf(a)
        }

        console.log(a)

        setTimeout(() => {
            setloading(false)
        }, 50);
        // navigation.navigate('MintboxAllocation', a)
    }




    const onDelete = (fund) => {
        // setselectedEtf(selectedEtf.filter(f => f.UID !== fund.UID));

        var a = unique([...selectedEtf.filter(f => f.UID !== fund.UID)], "UID")

        if (100 % a.length === 0) {
            a = a.map((obj, index) => ({ ...obj, allocation: Number((100 / a.length).toFixed(0)), locked: false, index }))
            setselectedEtf(a)

        } else {
            a = a.map((obj, index) => ({ ...obj, allocation: Number((100 / a.length).toFixed(0)), locked: false, index }))
            a[0].allocation = Number(a[0].allocation) + 1
            setselectedEtf(a)
        }

        console.log(a)

        setTimeout(() => {
            setloading(false)
        }, 50);
    }


    const checkValidation = () => {


        if (name !== '' && risk !== 0 && horizon !== 0 && fee !== '' && description !== '' &&
            selectedEtf.length >= 2 && isFixedAllo !== '' && rebalance !== 0) {

            var sum = 0
            selectedEtf.map((fund) => {
                sum += Number(fund.allocation)
            })

            console.log(sum)
            if (sum === 100) {
                return true
            }
            return false
        }

        return false


    }


    const onLock = (id) => {
        const newArr = selectedEtf.map(fund => {
            return fund.UID === id ? { ...fund, locked: !fund.locked } : fund
        })
        //check if atleast 1 etf is unlocked
        if (newArr.some(f => f.locked === false)) {
            setselectedEtf(newArr)
        } else {
            toast.error("Unlock some ETFs to readjust weights")
        }

        setTimeout(() => {
            setloading(false)
        }, 50);
    }

    const changeAllo = (text, index) => {


        var a = selectedEtf
        var b = a.filter(data => data.locked === true);
        a[index].locked = true
        b = a.filter(data => data.locked === true);

        if (a.length - b.length >= 1) {
            a[index].allocation = text
            var sum = 0;
            b.map((data, index) => {
                sum += parseInt(data.allocation);
            })
            // console.log("DSFFS", sum)
            var c = a.filter((data) => {
                return !b.includes(data);
            });
            // console.log(c)
            var finalamt = 100 - sum
            // console.log(finalamt)
            // console.log(((finalamt - (finalamt % c.length)) / c.length).toFixed(0))
            if (finalamt % c.length === 0) {
                c = c.map(obj => ({ ...obj, allocation: (finalamt / c.length).toFixed(0) }))
                setselectedEtf((b.concat(c)).sort((a, b) => a.index - b.index));
            } else {
                c = c.map(obj => ({ ...obj, allocation: ((finalamt - (finalamt % c.length)) / c.length).toFixed(0) }))
                c[0].allocation = Number(c[0].allocation) + (finalamt % c.length)
                setselectedEtf((b.concat(c)).sort((a, b) => a.index - b.index));
            }
        } else {
            toast.error("Unlock ateast 2 ETFs to readjust weights")
        }
        // cha()
        setTimeout(() => {
            setloading(false)
        }, 50);

    }


    const createMintbox = async () => {
        var aa = {
            name,
            fee,
            description,
            risk,
            horizon,
            isFixedAllo,
            rebalance,
            etfs: selectedEtf
        }

        console.log(aa)

        const x = selectedEtf.map((fund) => {
            return {
                fundCode: Number(fund.UID),
                indexId: 1,
                assetId: Number(fund.type),
                weightage: Number(fund.allocation)
            }
        })
        const final = {
            clientId: Number(clientID),
            name,
            description,
            risk: Number(risk),
            duration: Number(horizon),
            minInvestment: 1000,
            xirr: 10,
            constituents: x,
            rebalanceFrequency: Number(rebalance),
            videoLink: videoLink,
            fee: Number(fee) + ""
        }
        console.log("CREATE MINTBOX API", final)
        let res = await AdminServices.createMintbox(final).then((res) => JSON.stringify(res));
        res = await JSON.parse(res);
        if (res.type === true) {
            if (res.data.Result === 'Success') {
                console.log("Mintbox created", res.data)
                toast.success("Mintbox Created")
                // db.remove(db.model.createMintbox)
            }
            else {
                toast.error(res.data)
            }
        } else {
            toast.error()
        }


    }

    return <div>
        <h5>Create Mintbox</h5>

        <div class="flex row my-3">
            <div class="col mx-2 card">
                <div class="card-body">
                    <form>
                        <div class="row">
                            <div class="form-group col-md-6">
                                <label for="inputmintName">Mintbox Name</label>
                                <input onChange={(e) => setName(e.target.value)} value={name} type="text" class="form-control" id="inputmintName" placeholder="Enter Mintbox Name" />
                            </div>

                            <div class="form-group col-md-6">
                                <label for="clientid">ClientID ( 0 : all clients)</label>
                                <input onChange={(e) => setClientID(e.target.value)} value={clientID} type="text" class="form-control" id="clientid" placeholder="Enter Client ID" />
                            </div>
                        </div>

                        <div class="row my-4">

                            <div class="form-group col">
                                <label for="inputState">Investing horizon</label>
                                <select onChange={(e) => setHorizon(e.target.value)} value={horizon} id="inputState" class="form-control">
                                    <option value={0}>Choose...</option>

                                    <option value={1}>1-3 year</option>
                                    <option value={2}>3-5 year</option>
                                    <option value={3}>5-7 year</option>
                                    <option value={4}>7+ year</option>
                                </select>
                            </div>

                            <div class="form-group col">
                                <label for="inputState">Risk</label>
                                <select onChange={(e) => setRisk(e.target.value)} value={risk} id="inputState" class="form-control">
                                    <option value={0}>Choose...</option>

                                    <option value={1}>Very low</option>
                                    <option value={2}>Low</option>
                                    <option value={3}>Moderate</option>
                                    <option value={4}>High</option>
                                    <option value={5}>Agressive</option>
                                </select>
                            </div>
                        </div>

                        <h5 className="my-4">Add constituents</h5>

                        <SearchScreen route={{
                            params: {
                                text: "Search by ETF name",
                                type: 2,
                                header: "Invest in direct mutual funds",
                                searchType: 0,
                            }
                        }}
                            navigation={props.history}
                            type={0}
                            onClick={(item) => {
                                setloading(true)
                                allocateETF(item)
                            }}
                            isOpen={(bol) => setshow(bol)}
                        />


                    </form>
                </div>
            </div>
            <div class="col mx-2 card">
                <div class="card-body">
                    <div class="row">
                        <div class="form-group col-md-6">
                            <label for="fee">Fee</label>
                            <input onChange={(e) => setFee(e.target.value)} value={fee} type="text" class="form-control" id="fee" placeholder="Enter Mintbox fee" />
                        </div>

                        <div class="form-group col-md-6">
                            <label for="inpDur">Video URL</label>
                            <input onChange={(e) => setVideoLink(e.target.value)} value={videoLink} type="text" class="form-control" id="inpDur" placeholder="Enter Video Link" />
                        </div>
                    </div>


                    <div class="row my-4">

                        <div class="form-group col">
                            <label for="inputState">Rebalance type</label>
                            <select onChange={(e) => setRebalance(e.target.value)} value={rebalance} id="inputState" class="form-control">
                                <option value={0}>Choose...</option>

                                <option value={1}>every 3 months</option>
                                <option value={2}>every 6 months</option>
                                <option value={3}>every 12 months</option>
                                <option value={4}>Manually</option>
                                <option value={5}>No rebalance</option>
                            </select>
                        </div>

                    </div>


                    <div class="form-check form-check-inline my-4">
                        <input onChange={(e) => setisFixedAllo(e.target.value)} class="form-check-input" type="radio" name="inlineRadioOptions" id="Fixed" value="0" />
                        <label class="form-check-label" for="inlineRadio1">Fixed Allocation</label>
                    </div>
                    <div class="form-check form-check-inline my-4">
                        <input onChange={(e) => setisFixedAllo(e.target.value)} class="form-check-input" type="radio" name="inlineRadioOptions" id="Dynamic" value="1" />
                        <label class="form-check-label" for="inlineRadio2">Dynamic Allocation</label>
                    </div>


                    <div class="form-group my-3">
                        <label for="exampleFormControlTextarea1">Description</label>
                        <textarea onChange={(e) => setDescription(e.target.value)} value={description} class="form-control" id="exampleFormControlTextarea1" rows="5"></textarea>
                    </div>
                </div>
            </div>
        </div>


        <div class="flex row my-3">

            <div class="col mx-2 card">
                <div class="flex row my-3">

                    <h5 class="col my-4">Manage constituents</h5>

                    <button class=" my-2 mx-4 w-25"
                        style={{
                            borderRadius: 4,
                            fontSize: "14px",
                            backgroundColor: cssVar.getPropertyValue("--brandSecondary"),
                            color: "#FFFFFF",
                            height: "30px",
                            border: "none",
                            opacity: checkValidation() ? 1 : 0.4,
                        }}
                        onClick={checkValidation() ? () => {
                            createMintbox()
                        } : () => { }}
                    >
                        Create Mintbox
                    </button>
                </div>
                <h6 style={{ fontWeight: "700" }}>* NOTE: Allocation sum should be 100</h6>

                <div class="card-body">
                    <form>
                        {/* <div class="row">
                                <div class="form-group col-md-6">
                                    <label for="inputmintName">Search ETF</label>
                                    <input type="text" class="form-control" id="inputmintName" placeholder="Enter Mintbox Name" />
                                </div>

                            </div> */}





                        <div class="row my-2">

                            <div class="form-group col">


                                {loading ? <h2>...</h2> :


                                    selectedEtf.map((fund, index) => {
                                        return <div style={{ padding: "1%", borderBottom: "1px solid #e5e5e5" }} key={fund.UID}>
                                            <div style={{ display: "flex" }}>
                                                <img src={MintboxSmallCube} style={{ width: "20px", height: "20px", padding: "2px", marginRight: "4%", border: "1px solid #e5e5e5", borderRadius: 5 }} />
                                                <div style={{ flex: "1" }}>
                                                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "500" }}>{fund.ETFName}</div>
                                                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>{"NAV  " + fund.NAV}</div>
                                                </div>

                                                {
                                                    fund.locked ?
                                                        <BsFillLockFill size={16} style={{ color: cssVar.getPropertyValue('--red'), margin: "auto 1%" }} onClick={() => {
                                                            setloading(true)
                                                            onLock(fund.UID)
                                                        }} />
                                                        :
                                                        <BsFillUnlockFill size={16} style={{ color: cssVar.getPropertyValue('--primary'), margin: "auto 1%" }} onClick={() => {
                                                            setloading(true)
                                                            onLock(fund.UID)
                                                        }} />
                                                }
                                                <input type="text" maxLength={2} defaultValue={fund.allocation} style={{ width: "60px", height: "37px", margin: "auto 1%", textAlign: "center", border: "1px solid #e5e5e5", outline: "none", borderRadius: "4px", background: "#FAFAFA" }}

                                                    onBlur={(e) => {
                                                        setloading(true)
                                                        changeAllo(e.target.value, index)
                                                    }}
                                                // onBlur={e => assignWeights(index, parseInt(e.target.value))}
                                                // onKeyDown={e => { if (e.key === 'Enter') assignWeights(index, parseInt(e.target.value)) }}
                                                />
                                                <BiMinusCircle size={22} onClick={() => {
                                                    setloading(true)

                                                    onDelete(fund)
                                                }} style={{ background: "#F1FBF4", color: 'red', borderRadius: "50%", margin: "auto 0 auto 2%" }}
                                                />
                                            </div>
                                        </div>
                                    })}

                            </div>
                        </div>

                    </form>
                </div>
            </div>
        </div>
    </div>;

}
