import React from 'react';
import { Spinner } from 'react-bootstrap';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';
import Footer from './Footer';

export default function InvestToday(props) {

	const [investToday, setInvestToday] = React.useState("500000");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "0", "name": " 0" },
		{ "key": "100000", "name": " 1 Lakhs" },
		{ "key": "200000", "name": " 2 Lakhs" },
		{ "key": "500000", "name": " 5 Lakhs" },
	]

	const checkValidation = () => {
		if (investToday!=='0') {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	},[investToday])

	return (
		<div>
			<MainHeader
				header="Plan child education"
				right="Home"
				screenType="goalScreen"
				navigation={props.history}
			/>
			<ProgressBar now={60} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">How much can you invest for this goal today?</div>
							<input
								type="tel"
								id="investToday"
								className='normalInput rupee'
								style={{color:"#294C88"}}
								placeholder='50,000'
								value={investToday}
								onChange={(num) => {
									const a = Number(num.target.value.replace(/\D/g, '')).toString()
									setInvestToday(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row',overflowX:'auto',paddingBottom:'10px' }}>
								{chipset.map((item) => {
									return (
										<Chip
											className={investToday===item.key?"chip-b selected":"chip-b"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setInvestToday(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/financial-goal/increase-monthly-contribution"
				state={{...props.location.state,investToday}}
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
