import React from 'react';
import { Spinner } from 'react-bootstrap';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';
import Footer from './Footer';

export default function PresentCost(props) {

	const [presentCost, setPresentCost] = React.useState("500000");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "1000000", "name": " 10 Lahks" },
		{ "key": "2500000", "name": " 25 Lakhs" },
		{ "key": "4000000", "name": " 40 Lakhs" }
	]

	const addCommas = (str) => {
		if(str.length<=3){
			return str;
		}
		return addCommas(str.slice(0,-3)) + "," +str.slice(-3)
	}
	const checkValidation = () => {
		if (presentCost!=='0') {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	},[presentCost])

	return (
		<div>
			<MainHeader
				header="Plan child education"
				right="Home"
				screenType="goalScreen"
				navigation={props.history}
			/>
			<ProgressBar now={20} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">Present cost of planned education</div>
							<input
								type="tel"
								id="presentCost"
								className='normalInput rupee'
								style={{color:"#294C88"}}
								placeholder='10,00,000'
								value={presentCost}
								onChange={(num) => {
									const a = Number(num.target.value.replace(/\D/g, '')).toString()
									setPresentCost(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row',overflowX:'auto',paddingBottom:'10px' }}>
								{chipset.map((item) => {
									return (
										<Chip
											className={presentCost===item.key?"chip-b selected":"chip-b"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setPresentCost(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/financial-goal/goal-due"
				state={{...props.location.state,presentCost}}
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
