import React from 'react';
import { Switch } from 'react-router-dom';
import { Route } from 'react-router-dom/cjs/react-router-dom.min';
import Admin from 'views/admin/layouts/Admin';
import Login from 'views/admin/views/Login';
import AdminProtectedRoutes from 'views/mobileView/components/molecules/Routers/AdminProtectedRoutes';
import Home from '../screens/marketing/Home';
import Sample from '../screens/marketing/sample';




const WebHomeLayout = () => {
  return (
    <Switch>

      {/* -------- Route is for MarketingScreens -------- */}
      <Route exact path={`/`} component={Home} />
      <Route exact path={`/sample`} component={Sample} />
      <AdminProtectedRoutes path="/mint-admin" component={Admin} />
      <Route path="/lo-admin" component={Login} />

      <Route path={`/*`} component={Sample} />

      
    </Switch>
  );
};

export default WebHomeLayout;
