import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';
import AdminServices from 'services/api/AdminServices';
import { VpnLock } from '@material-ui/icons';

export default function CapabilityPageResult(props) {

	const [riskCapability,setRiskCapability] = React.useState()

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const riskText = ['very low','low','moderate','high','aggressive']
	React.useEffect(()=>{
		loadData()
	},[])

	const loadData = async () => {
		setLoading(true);
		db.get(db.model.user, true).then((p) => {
			getRiskCapability(p.userData.id)
		})	
	}

	const getRiskCapability = async (id) => {
		if(Number(props.location.state.age)>55){
			setRiskCapability(3)
			setLoading(false)
			setNextDisabled(false)
			return;
		}
		const data = {
			"clientId":	id,
			"currentAge": Number(props.location.state.age),
			"noDependents": Number(props.location.state.dependents),
			"annualIncome": Number(props.location.state.income),
			"emergencyFund": Number(props.location.state.emergencyFund),
			"insuranceCover": Number(props.location.state.lifeInsurance),
			"isDependentInsured": Number(props.location.state.healthInsurance),
			"portfolioPreference": 0,
			"planofAction": 0
		}
		console.log(data)
		let res = await AdminServices.getRiskScore(data).then((res) => JSON.stringify(res));
		res = await JSON.parse(res);
		console.log("Get Risk API: ", res.data)
		if (res.type === true) {
			setRiskCapability(res.data.RiskCapability)
			setLoading(false)
			setNextDisabled(false)
		}
	}
		
	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={63} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
						  <div className='textHead' style={{fontWeight:"500"}}>We have identified your risk taking capability as
						  <span className='textHead' style={{color:'red'}}> {riskText[riskCapability-1]}</span></div>
						  <div className='textHead' style={{marginTop:"6%"}}>Please continue to identify your risk taking willingness now for full risk analysis</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-willingness-1"
				state={{...props.location.state,riskCapability}}
				buttonText="Continue"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}